<template>
  <div class="project page-container d-flex">
    <div class="info-wrap pr-8">
      <h3 class="mb-2">Company</h3>
      <p class="mb-8 text-light">{{ currentPorject.company }}</p>
      <h3 class="mb-2">Team</h3>
      <p class="mb-8 text-light">{{ currentPorject.team }}</p>
      <h3 class="mb-2">Year</h3>
      <p class="mb-8 text-light">{{ currentPorject.year }}</p>
      <h3 class="mb-2">My Deliverable</h3>
      <p class="mb-8 text-light">{{ currentPorject.deliverable }}</p>
      <h3 class="mb-2">Software</h3>
      <p class="mb-8 text-light pre-wrap">{{ currentPorject.software }}</p>
    </div>
    <div class="flex-1 overflow-hidden">
      <h1 class="mb-8">{{ currentPorject.name }}</h1>
      <template
        v-if="
          !!currentPorject.paragraphs && currentPorject.paragraphs.length > 0
        "
      >
        <div v-for="(v, i) in currentPorject.paragraphs" :key="i" class="info-paragraph mb-5">
          <h2 class="mb-0" v-html="v.title"></h2>
          <template v-for="(item, index) in v.content">
            <img
              :key="index + 'i'"
              v-if="item.type && item.type == 'img'"
              class="info-img mb-5"
              :class="item.class"
              :src="getImg(item.url)"
            />
            <div :key="index + 'i'" :class="item.class" v-else-if="item.type && item.type == 'rich'" v-html="item.content"></div>
            <div :key="index + 'i'" class="slide-box" :class="item.class" v-else-if="item.type && item.type == 'slide'">
              <template v-for="(temp, idx) in item.images">
                <img
                  :key="idx"
                  class="slide-img"
                  :class="getClass(idx, item.name)"
                  :src="getImg(temp)"
                  @mouseenter="onHover(idx, item.name)"
                />
              </template>
            </div>
            <p class="info-text mt-2 mb-5" :key="index + 'p'" v-else v-html="item"></p>
          </template>
        </div>
      </template>
    </div>

    <!--  -->
  </div>
</template>
      
      <script>
import { PROJECT_DETAILS, SLIDE_INDICES } from "@/constant";
export default {
  name: "ProjectDetail",
  components: {},
  data() {
    return {
      PROJECT_DETAILS,
      projectName: "",
      currentPorject: {},
      slideIndices: {}
    };
  },
  computed: {
    getImg() {
      return function (url) {
        return require(`@/assets/${url}`);
      };
    },
    getClass() {
      return function(index, name) {
        const chosen = this.slideIndices[name];
        if(index == chosen) {
          return 'active';
        } else if(index == chosen + 1 || index == chosen - 1) {
          return 'secondary';
        }
        return '';
      }
    }
  },
  methods: {
    onHover(index, name) {
      this.slideIndices = { ...this.slideIndices, [name]: index };
    }
  },
  mounted() {
    const {
      params: { projectName },
    } = this.$route;
    this.projectName = projectName;
    this.currentPorject = { ...this.PROJECT_DETAILS[projectName] };
    this.slideIndices = { ...SLIDE_INDICES };
  },
};
</script>
      
      <style>
@import "../style/project.scss";
</style>
      